@import url(https://fonts.googleapis.com/css?family=Nunito:300,400,700);
.sr-only {
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}
* {
  box-sizing: border-box;
}
body {
  font-size: 18px;
  font-family: "Nunito", sans-serif;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  /* width: 100%; */
  min-height: 100vh;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

button,input,optgroup,select,textarea{font-family:inherit;font-size:100%;line-height:1.15;margin:0}[type="checkbox"],[type="radio"]{box-sizing:border-box;padding:0}.c-toggle label{font-size:calc(12px * 1.75);cursor:pointer;margin:0 0.5em}.c-toggle__wrapper{font-size:12px;display:inline-block;vertical-align:middle;width:7em;height:3.5em;border-radius:3.5em;border:1px solid #ddd;position:relative}.c-toggle input[type="radio"]{display:inline-block;width:50%;height:100%;position:relative;margin-right:-2px;z-index:1;cursor:pointer;opacity:0}.c-toggle input[type="radio"]:disabled ~ .c-toggle__switcher{opacity:0.4}.c-toggle input[type="radio"]:focus ~ .c-toggle__switcher{top:-2px;border:3px solid #a1456e}.c-toggle input[type="radio"]:nth-child(2):checked ~ .c-toggle__background{background-color:#333}.c-toggle input[type="radio"]:nth-child(2):checked ~ .c-toggle__switcher{right:0;left:50%}.c-toggle__background{display:block;position:absolute;top:0;left:0;width:100%;height:100%;z-index:0;border-radius:3.5em;background-color:#eee;transition:all 0.1s ease-out}.c-toggle__switcher{display:block;position:absolute;top:0;left:0;right:100%;width:50%;height:100%;border-radius:50%;background-color:#d65c93;z-index:2;transition:all 0.1s ease-out;box-sizing:content-box}fieldset{border:none}fieldset legend{text-align:center}.range-slider{text-align:center;width:100%}.range-slider input[type="range"]{-webkit-appearance:none;-moz-appearance:none;appearance:none;margin:1rem 0;width:100%}.range-slider input[type="range"]:disabled::-webkit-slider-runnable-track{opacity:0.4}.range-slider input[type="range"]:focus{outline:none}.range-slider input[type="range"]:focus::-webkit-slider-thumb,.range-slider input[type="range"]:active::-webkit-slider-thumb{border:4px solid rgba(214,92,147,0.5)}.range-slider input[type="range"]::-webkit-slider-thumb{border:2px solid #b1b2b4;height:36px;width:36px;border-radius:50%;background:#ffffff;cursor:pointer;-webkit-appearance:none;appearance:none;margin-top:-14px}.range-slider input[type="range"]::-webkit-slider-runnable-track{width:100%;height:6px;cursor:pointer;background:#dbdbdc;border-radius:1.3px}.display{background-color:rgba(223,226,225,0.71);color:#1b1d1c;border-radius:3px;padding:0.5rem 0.2rem;text-align:center;font-weight:700;letter-spacing:0.1rem;width:100%;min-height:2.5em;-webkit-user-select:text;-ms-user-select:text;user-select:text}.drum-pad{display:inline-block;width:4rem;height:4rem;margin-right:0.5rem;margin-top:0.5rem;border-radius:7px;border:0;outline:0;background-color:grey;box-shadow:3px 3px 5px black;cursor:pointer}.drum-pad:active{background-color:orange;box-shadow:unset}.drum-pad:focus-visible{border:3px solid orangered}.drum-pads{width:15rem;display:flex;flex-flow:row wrap}@media all and (max-width: 768px){.drum-pads{width:13.5rem}}.drum-pads button.active{background-color:orange;box-shadow:3px 3px 5px orange}.drum-machine{display:flex;flex-flow:row wrap;width:768px;touch-action:manipulation}@media all and (max-width: 768px){.drum-machine{width:100%;flex-direction:column}}.drum-machine .drums{flex-basis:50%;align-self:center}.drum-machine .drum-controls{display:flex;flex-direction:column;align-items:center;flex-basis:50%}@media all and (max-width: 768px){.drum-machine .drum-controls{margin-top:1rem}}.drum-controls>*+*{margin-top:0.5rem}.text-center{text-align:center}

